import {AContainer} from "./abstract/a-container";
import {Button} from "../button/button";
import {IRenderable} from "../core/interface/i-renderable";
import jquery = require("jquery");

/**
 * Este container agrupa um conjunto de bot√µes.
 * 
 * <img src="../../doc_assets/toolbar_exemple.png">
 * 
 * @example
 * import {Button} from "mvcomponents/button";
 * import {ViewPager, Box, ToolBar} from "mvcomponents/container";
 * import {Label} from "mvcomponents/widget";
 * import {LoginForm} from "./login-form";
 *
 * let toolBarContainer: ToolBar = new ToolBar()
 *      .append(new Button()
 *          .setLabel("Adicionar")
 *          .setIcon("glyphicon glyphicon-plus"))
 *      .append(new Button()
 *          .setLabel("Editar")
 *          .setIcon("glyphicon glyphicon-edit"))
 *      .append(new Button()
 *          .setLabel("Excluir")
 *          .setIcon("glyphicon glyphicon-remove"))
 *      .append(new Button();
 *          .setLabel("Clique aqui para ver um alert()"));
 *
 * let page:ViewPager = new ViewPager()
 *      .setCssProperties({"padding-top": "15px"})
 *      .append(new Box()
 *          .append(toolBarContainer))
 *      .appendTo("#conteudo");
 * 
 * @export
 * @memberof module:mvcomponents/container
 * @extends {AContainer}
 */
export class ToolBar extends AContainer<Button>{
	constructor() {
		super("div", "");
		jquery(this.element)
			.addClass("ToolBar btn-group col-sm-offset-0 col-sm-12 col-xs-12")
			.css({ "margin-bottom": "10px" });
	}
}
